import './RuleSettingsPage.styl';

import React, {
  Component,
} from 'react';

import {
  Header,
  HeaderBottom,
  RuleSettingsBody,
  RulesListCompany,
  ApplySmallContainer
} from 'components';
import PropTypes from 'prop-types';

import { bindActionCreators }       from 'redux';
import { connect }                  from 'react-redux';

import {
  CompanyActions
} from 'actions';

@connect(state => ({
  companyData: state.companyReducer
}), dispatch => ({
  companyActions: bindActionCreators(CompanyActions, dispatch)
}))


export default class RuleSettingsPage extends Component {
  static propTypes = {
    companyData: PropTypes.object,
    match: PropTypes.object,
    companyActions: PropTypes.object
  };

  constructor(props) {
    super(props);

    this.state = {
      statusAllCompanings: false
    };
  }

  componentWillMount() {
    const params = this.props.match.params.id;

    if (params) {
      const {
        ruleData
      } = this.props.companyData;

      this.props.companyActions.getRule(ruleData, params);
    }
  }

  changeStatusCompanings() {
    this.setState({ statusAllCompanings: !this.state.statusAllCompanings });
  }

  render() {
    const {
      statusAllCompanings
    } = this.state;

    const {
      companyData,
      rule
    } = this.props.companyData;

    const params = this.props.match.params.id;

    return (
      <div className="c-rule-settings-page">
        <div className="rules-page-container">
          <Header
            indexPage={1}
          />
          <HeaderBottom
            bigTitle="NEW RULE"
            smallTitle="ALL CAMPAINGS"
            left
            showSearchSmall
            borderSmall
          />
          <div className="rules-body-container">
            <div className="rules-left-container">
              <RuleSettingsBody
                status={statusAllCompanings}
                changeStatus={this.changeStatusCompanings.bind(this)}
                rule={rule}
                params={params}
              />
            </div>
            <div className="rules-right-container">
              <ApplySmallContainer
                status={statusAllCompanings}
                changeStatus={this.changeStatusCompanings.bind(this)}
              />
              <RulesListCompany
                checkShow
                status={statusAllCompanings}
                data={companyData}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}



